.userProfile{padding:2rem;max-width:500px;margin:0 auto}.title{font-size:1.8rem;margin-bottom:.5rem}.desc{font-size:1rem;margin-bottom:1.5rem;color:gray}.form{display:flex;flex-direction:column;gap:1rem}.form label{display:flex;flex-direction:column;font-weight:bold}.form label.light{color:var(--secondary)}.form label.dark{color:var(--secondary2)}.form label input{margin-top:.3rem;padding:.5rem;border:1px solid #ddd;border-radius:5px}.form label input.light{background-color:var(--white);color:var(--on-surface)}.form label input.dark{background-color:var(--dark2);color:var(--surface)}